.App {
  text-align: center;
}

div { box-sizing: border-box;}
button { cursor: pointer; }
h4 span { cursor: pointer; margin-left: 5px; }

.list {
    padding: 0 20px;
    text-align: left;
    border-bottom: 1px solid grey;
}

.list .delete_btn {
    margin-left: 10px;
    background: antiquewhite;
    border: 0;
    padding: 5px 10px;
}

.input_wrap{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}
.input_wrap input {padding: 5px;}
.input_wrap button {
    padding: 6px 12px;
    font-size: 12px;
    border:0;
    margin-left: 3px;
    background: indianred;
    color: #fff;
}

.black_nav {
    display: flex;
    background: linear-gradient(45deg, palevioletred, #ffb9d0);
    width: 100%;
    color: #fff;
    padding-left: 20px;
}

.modal {
    margin: 20px 0;
    padding: 20px;
    background: lavenderblush;
    text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
